<template>
  <div style="height:100%;background:#FFFFFF;width:100%;">
    <page-header @btn-action="openApp" />
    <div class="pageView">
      <div style="color:#999999;margin-bottom:10px;">验证码:6位数字</div>
      <div>
        <van-field v-model="msgCode" placeholder="请输入验证码">
          <van-button
            slot="button"
            size="small"
            @click="sendMsgCode"
            :disabled="!btnEnable"
          >{{msgBtnText}}</van-button>
        </van-field>
      </div>
      <div class="bottomView">
        <div class="nextBtn" @click="commitAction">确 认</div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import { Field } from "vant";
import { Button } from "vant";
import PageHeader from "./PageHeader";

Vue.use(Button);
Vue.use(Field);
import {
  approvalIdenti,
  getMsgCode,
  getRsaPublicKey,
} from "../../service/verifyApplyService";
export default {
  name: "CommitPage",
  components: {
    PageHeader,
  },
  data() {
    return {
      msgCode: "",
      uuid: "",
      phone: "",
      resultType: "",
      btnEnable: true,
      msgBtnText: "发送验证码",
      icon: {
        xwjlogo: require("../../assets/share/shareLogo.png"),
      },
      publicKey: null,
      firstGetPubKey: true,
    };
  },
  methods: {
    getPubKey() {
      getRsaPublicKey()
        .then((res) => {
          if (res.resultCode == 0) {
            this.publicKey = res.data.publicKey;
            if (!this.firstGetPubKey && this.publicKey) {
              this.sendMsgCode();
            }
          } else {
            this.$toast(res.msg);
          }
          this.firstGetPubKey = false;
        })
        .catch((error) => {
          this.firstGetPubKey = false;
        });
    },
    sendMsgCode() {
      if (!this.publicKey) {
        this.getPubKey();
        return;
      }
      let rsaPhone = Vue.prototype.$getRSASign(
        this.phone + ",13",
        this.publicKey
      );
      let param = {
        accntInfo: rsaPhone,
      };
      getMsgCode(param)
        .then((data) => {
          if (data.resultCode == 0) {
            this.timeCountDown();
          }
          this.$toast(data.msg);
        })
        .catch((error) => {
          console.log(error);
          // this.$toast(error);
        });
    },
    timeCountDown() {
      this.btnEnable = false;
      var timeNumber = 60;
      let clock = window.setInterval(() => {
        timeNumber--;
        if (timeNumber > 0) {
          this.msgBtnText = timeNumber + "秒后重新发送";
        } else {
          this.btnEnable = true;
          window.clearInterval(clock);
          this.msgBtnText = "获取验证码";
        }
      }, 1000);
    },
    commitAction() {
      if (this.msgCode == "") {
        this.$toast("验证码不能为空");
        return;
      }
      let param = {
        uuid: this.uuid,
        phone: this.phone,
        verificationCode: this.msgCode,
        approvalResult: this.resultType,
        tagCode: JSON.parse(this.$route.query.identify).tagCode,
      };
      approvalIdenti(param)
        .then((data) => {
          console.log(data);
          this.parasData(data);
        })
        .catch((error) => {
          console.log(error);
          // this.$toast(error);
        });
    },
    parasData(data) {
      let userData = JSON.parse(this.$route.query.data);
      let roomStr =
        "【" +
        userData.areaName +
        "-" +
        userData.buildName +
        "-" +
        userData.showRoomName +
        "】";
      let param = {
        nickName: userData.ownerNickName,
        room: roomStr,
        type: this.$route.query.type,
        tagName: JSON.parse(this.$route.query.identify).tagName,
      };
      console.log(this.$route.query);
      console.log(param);
      this.$router.push({ path: "/resultPage", query: param });
    },
    openApp() {
      this.shareOpenApp("myfamily");
    },
  },
  mounted() {
    let query = this.$route.query;
    let dataObj = JSON.parse(query.data);
    console.log(dataObj);
    this.uuid = dataObj.uuid;
    this.phone = query.phone;
    this.resultType = query.type;
    this.getPubKey();

    this.androidShareOpenApp("myfamily"); //android 拉起APP
  },
};
</script>

